@import "/Users/hamsh/Instagram-clone/sukhwagram/frontend/src/config/_variables.scss";
.container {
  .spinner {
    height: 32px;
    animation: spinner 1.2s steps(12) infinite;
  }
  z-index: 1;
}

@keyframes spinner {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


